
'use client';

import Link from 'next/link';
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from '@/components/ui/card';
import { usePageBackground } from '@/hooks/usePageBackground';
import { cn } from '@/lib/utils';
import { Star, ShieldCheck, Gem } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { useEffect, useState } from 'react';
import { Skeleton } from '@/components/ui/skeleton';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';
import { Button } from '@/components/ui/button';

interface Level {
  id: number;
  title: string;
  category: string;
  isHard?: boolean;
}

const gameLevels: Record<string, Level[]> = {
  "Small Sister": [
    { id: 1, title: '+4 Formula' },
    { id: 2, title: '+3 Formula' },
    { id: 3, title: '+2 Formula' },
    { id: 4, title: '+1 Formula' },
    { id: 5, title: '-4 Formula' },
    { id: 6, title: '-3 Formula' },
    { id: 7, title: '-2 Formula' },
    { id: 8, title: '-1 Formula' },
    { id: 9, title: 'Small Sister Challenge', isHard: true },
  ],
  "Big Brother": [
    { id: 10, title: '+9 Formula' },
    { id: 11, title: '+8 Formula' },
    { id: 12, title: '+7 Formula' },
    { id: 13, title: '+6 Formula' },
    { id: 14, title: '+5 Formula' },
    { id: 15, title: '+4 Formula' },
    { id: 16, title: '+3 Formula' },
    { id: 17, title: '+2 Formula' },
    { id: 18, title: '+1 Formula' },
    { id: 19, title: '-9 Formula' },
    { id: 20, title: '-8 Formula' },
    { id: 21, title: '-7 Formula' },
    { id: 22, title: '-6 Formula' },
    { id: 23, title: '-5 Formula' },
    { id: 24, title: '-4 Formula' },
    { id: 25, title: '-3 Formula' },
    { id: 26, title: '-2 Formula' },
    { id: 27, title: '-1 Formula' },
    { id: 28, title: 'Big Brother Challenge', isHard: true },
  ],
  "Combination": [
    { id: 29, title: '+9 Formula' },
    { id: 30, title: '+8 Formula' },
    { id: 31, title: '+7 Formula' },
    { id: 32, title: '+6 Formula' },
    { id: 33, title: '-9 Formula' },
    { id: 34, title: '-8 Formula' },
    { id: 35, title: '-7 Formula' },
    { id: 36, title: '-6 Formula' },
    { id: 37, title: 'Combination Challenge', isHard: true },
  ],
  "Final Challenge": [
     { id: 38, title: 'Final Challenge', isHard: true },
  ],
  "Mastery Mix": [
    { id: 39, title: 'Mastery Mix 1' },
    { id: 40, title: 'Mastery Mix 2' },
    { id: 41, title: 'Mastery Mix 3' },
    { id: 42, title: 'Mastery Mix 4' },
    { id: 43, title: 'Mastery Mix 5' },
    { id: 44, title: 'Mastery Mix 6' },
    { id: 45, title: 'Mastery Mix 7' },
    { id: 46, title: 'Mastery Mix 8' },
    { id: 47, title: 'Mastery Mix 9' },
    { id: 48, title: 'Mastery Mix 10' },
    { id: 49, title: 'Mastery Mix 11' },
    { id: 50, title: 'Grandmaster Challenge', isHard: true },
  ]
};

const categoryIcons: Record<string, React.ElementType> = {
  "Small Sister": Star,
  "Big Brother": ShieldCheck,
  "Combination": Gem,
  "Final Challenge": Star,
  "Mastery Mix": Gem
}

const LevelButton = ({ level, isLocked, isCompleted }: { level: Level; isLocked: boolean; isCompleted: boolean; }) => {
  const Icon = level.isHard ? Star : categoryIcons[level.category] || Gem;

  return (
    <Button asChild variant={isCompleted ? "default" : "outline"} className={cn("h-12 w-full justify-start", isLocked && "bg-muted text-muted-foreground hover:bg-muted cursor-not-allowed")} disabled={isLocked}>
      <Link href={isLocked ? '#' : `/game/level-${level.id}`}>
        <div className="flex items-center gap-4">
          <div className={cn(
            "w-8 h-8 flex items-center justify-center rounded-full text-white",
            isCompleted ? "bg-green-500" : "bg-primary/50"
          )}>
            <span className="font-bold">{level.id}</span>
          </div>
          <span className="font-semibold">{level.title}</span>
        </div>
      </Link>
    </Button>
  );
};


export default function GameHomePage() {
  usePageBackground('#fbcfe8');
  const { user, getCompletedGameLevels } = useAuth();
  const [completedLevels, setCompletedLevels] = useState<number[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (user) {
      getCompletedGameLevels()
        .then(levels => {
          setCompletedLevels(levels);
          setIsLoading(false);
        });
    } else {
      setIsLoading(false);
    }
  }, [user, getCompletedGameLevels]);

  if (isLoading) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Loading Your Adventure...</CardTitle>
          <CardDescription>Please wait while we get your progress.</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {[...Array(5)].map((_, i) => <Skeleton key={i} className="h-12 w-full" />)}
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="space-y-8 max-w-2xl mx-auto">
      <div className="text-center">
        <h1 className="text-5xl font-extrabold tracking-tight text-pink-600 font-headline drop-shadow-lg sm:text-6xl">Bubble Game Adventure</h1>
        <p className="mt-4 max-w-2xl mx-auto text-lg text-pink-800/80">
          Pop your way through the levels to master your abacus skills!
        </p>
      </div>

      <Accordion type="single" collapsible className="w-full" defaultValue="item-0">
        {Object.entries(gameLevels).map(([category, levels], index) => {
            const CategoryIcon = categoryIcons[category] || Gem;
            return (
              <AccordionItem key={category} value={`item-${index}`}>
                <AccordionTrigger className="text-xl font-bold hover:no-underline">
                  <div className="flex items-center gap-3">
                    <CategoryIcon className="w-6 h-6 text-primary" />
                    {category}
                  </div>
                </AccordionTrigger>
                <AccordionContent>
                  <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 pt-4">
                    {levels.map(level => {
                      const isLocked = user ? level.id > 1 && !completedLevels.includes(level.id - 1) : level.id > 1;
                      const isCompleted = completedLevels.includes(level.id);
                      return <LevelButton key={level.id} level={{...level, category}} isLocked={isLocked} isCompleted={isCompleted} />;
                    })}
                  </div>
                </AccordionContent>
              </AccordionItem>
            );
        })}
      </Accordion>
    </div>
  );
}
